import React from "react";

function Div(props) {
  const style ={
    base:{
      width: "max-content",
      margin: "1rem auto",
      display: props.display,
      alignItems: props.alignItems,
    }
  }
  return (
    <div style ={style.base}>
        {props.children}
    </div>
  );
}

export default Div;
